.home-prod-wrapper{position:relative;width:46%;text-align:center}.home-prod-wrapper:nth-last-child(1):nth-child(odd){margin:auto}.home-prod-wrapper .home-prod-img{width:100%}.home-prod-wrapper .home-prod-img img{object-fit:cover;max-width:100%}.home-prod-wrapper .home-prod-img:hover .home-prod-hover{opacity:1;z-index:4}.home-prod-wrapper .home-prod-name{font-size:22px;font-weight:bold;padding:10px 20px;text-align:center}.home-prod-wrapper .home-prod-desc{font-size:17px;text-transform:capitalize;font-weight:400;font-style:italic;color:gray;padding:20px 20px}.home-prod-wrapper .home-prod-info-block{position:relative;bottom:30px;background:#fff;width:85%;margin:auto;border:1px rgba(0,0,0,0.301) solid}.home-prod-wrapper .home-prod-hover{padding-bottom:20px;z-index:-1;transition:.4s all ease;opacity:0;position:absolute;width:100%;min-height:calc(100% - 30px);top:0;background-color:rgba(0,0,0,0.795);display:flex;justify-content:center;align-items:center;flex-direction:column;box-shadow:0px 0px 10px 10px rgba(0,0,0,0.5)}.home-prod-wrapper .home-prod-hover .home-prod-ingredients{text-transform:capitalize;display:flex;flex-direction:column;justify-content:center;color:#fff;font-size:1.2em;letter-spacing:1px}.home-prod-wrapper .home-prod-hover .home-prod-ingredients-title{color:orangered;font-weight:bold;font-size:1.4em;padding:10px}.home-prod-wrapper .home-prod-hover .home-prod-price{padding:30px;font-size:35px;color:orangered;font-weight:bold}.home-prod-wrapper .home-prod-hover .home-prod-order button{transition:.4s all ease;background-color:orangered;color:#fff;text-transform:uppercase;border:0;width:220px;height:60px;border-radius:2px;font-size:20px}.home-prod-wrapper .home-prod-hover .home-prod-order button:focus{outline:none}.home-prod-wrapper .home-prod-hover .home-prod-order button:hover{cursor:pointer;background-color:red}@media screen and (max-width: 768px){.home-prod-wrapper{width:100%;margin:0 10px}}

.home-prod-list-title{text-align:center;text-transform:capitalize;padding-bottom:20px}.home-product-list{display:flex;flex-wrap:wrap;justify-content:space-between}

.product-item-wrapper{border:1px rgba(255,68,0,0.5) solid;margin:15px;transition:.5s all ease;width:220px;box-shadow:0 0 10px 0px rgba(0,0,0,0.425)}.product-item-wrapper:hover{transition:.4s all ease;transform:scale(1.1, 1.1)}.product-item-wrapper .product-info-active{position:relative;bottom:200px;z-index:0;transition:.5s all ease;overflow:hidden;height:0px}.product-item-wrapper .product-item-img img{transform-origin:top left;transition:.8s all ease;width:100%;object-fit:cover}.product-item-wrapper .product-item-info{text-align:center}.product-item-wrapper .product-item-info .product-item-price{font-size:26px;color:orangered;font-weight:bold;padding-bottom:10px}.product-item-wrapper .product-item-info .product-info-title{min-height:60px;text-align:center;padding:5px 0;font-size:1.3em}.product-item-wrapper .product-item-info .product-info-details{transition:.4s all ease;background-color:orangered;color:#fff;text-transform:uppercase;border:0;width:135px;height:40px;border-radius:2px;font-size:16px;margin:10px 0;border-radius:2px;letter-spacing:1px}.product-item-wrapper .product-item-info .product-info-details:hover{cursor:pointer;background:orange}.product-item-wrapper.active{border:none;box-shadow:none;width:100%;padding:0 24%}.product-item-wrapper.active:hover{transform:scale(1, 1)}.product-item-wrapper.active .product-info-active{display:flex;justify-content:center;align-items:center;flex-direction:column;border:1px solid orangered;position:relative;bottom:40px;z-index:4;background:#fff;margin:auto;height:auto;min-height:120px;width:85%;box-shadow:0 -20px 40px 15px rgba(0,0,0,0.486)}.product-item-wrapper.active .product-info-active .product-active-title{padding:10px;text-align:center;color:orangered}.product-item-wrapper.active .product-info-active .product-active-desc{text-align:center;padding:0 10px;font-size:17px;font-weight:bold;font-style:italic}.product-item-wrapper.active .product-info-active .product-active-order-b{font-style:normal;margin:10px 0}.product-