.lds-ellipsis{display:inline-block;position:relative;width:80px;height:80px}.lds-ellipsis div{position:absolute;top:33px;width:13px;height:13px;border-radius:50%;background:#000;animation-timing-function:cubic-bezier(0, 1, 1, 0)}.lds-ellipsis div:nth-child(1){left:8px;animation:lds-ellipsis1 0.6s infinite}.lds-ellipsis div:nth-child(2){left:8px;animation:lds-ellipsis2 0.6s infinite}.lds-ellipsis div:nth-child(3){left:32px;animation:lds-ellipsis2 0.6s infinite}.lds-ellipsis div:nth-child(4){left:56px;animation:lds-ellipsis3 0.6s infinite}@keyframes lds-ellipsis1{0%{transform:scale(0)}100%{transform:scale(1)}}@keyframes lds-ellipsis3{0%{transform:scale(1)}100%{transform:scale(0)}}@keyframes lds-ellipsis2{0%{transform:translate(0, 0)}100%{transform:translate(24px, 0)}}

.product-list{display:flex;flex-wrap:wrap;justify-content:center}.product-list-title{font-size:50px;color:orangered;text-align:center;padding:15px 0 30px 0;text-transform:capitalize}

.header-menu-wrapper{transition:ease .5s all;z-index:10;position:-webkit-sticky;position:sticky;top:0;box-shadow:0px 0px 50px 20px rgba(0,0,0,0.603);font-size:14px;font-family:'Quicksand', sans-serif;text-transform:uppercase;padding:15px 20px 20px 20px;width:100%;max-width:100%;min-height:50px;max-height:100px;background-color:#f1f1f1;display:flex;align-items:flex-end}.header-menu-wrapper .menu-burger{display:none}.header-menu-wrapper.menu-sticky{max-height:75px}.menu-logo{transition:.5s all ease;box-shadow:-10px -10px 30px 10px rgba(0,0,0,0.603);background-color:#f1f1f1;fill:red;position:relative;top:60px;left:15px;width:20%;max-width:300px;min-width:230px}.menu-logo .menu-logo-svg{max-width:100%}.menu-logo.menu-sticky{max-width:150px;min-width:100px;box-shadow:0 0 0 0;top:15px;left:0}.menu-socials{width:15%}.menu-socials i{transition:.3s all ease;font-size:20px;padding:3px 12px}.menu-socials i:hover{cursor:pointer;color:gray}.menu-cat-wrapper{transition:.5s all ease;font-weight:bold;padding:0 15px;width:60%;display:flex;justify-content:space-around}.menu-cat-wrapper .menu-socials-responsive{display:none}.menu-cat-wrapper.menu-sticky{justify-content:flex-start;width:70%}.menu-cat-wrapper.menu-sticky .menu-cat-item{padding-right:25px}.menu-cat-item{transition:.3s all ease;padding:2px;z-index:2;position:relative}.menu-cat-item.special{color:orangered;border-left:1px gray solid;padding-left:20px}.menu-cat-item.special .menu-cat-hover{left:20px}.menu-cat-item:hover{cursor:pointer;color:orangered}.menu-cat-item:hover .menu-cat-hover{width:70px}.menu-cat-item .menu-cat-hover{z-index:1;left:0;bottom:-5px;position:absolute;transition:.3s all ease;width:0px;height:13px;background-color:rgba(255,102,0,0.171)}.menu-cart{color:#ff0800;width:5%}.menu-cart i{transition:.3s all ease;font-size:30px;padding:3px 10px}.menu-cart i:hover{cursor:pointer;color:gray}@media screen and (max-width: 991px){.header-menu-wrapper.active .menu-cat-wrapper{font-size:14px;height:calc(100vh - 100%)}.header-menu-wrapper .menu-cart{position:absolute;right:120px}.header-menu-wrapper .menu-socials{display:none}.header-menu-wrapper .menu-logo{top:10px;left:0;min-width:145px;box-shadow:none}.header-menu-wrapper .menu-cat-wrapper{font-size:0;z-index:99;background:rgba(255,47,47,0.788);flex-direction:column;align-items:center;justify-content:space-around;position:absolute;height:0vh;overflow:hidden;width:100vw;top:100%;margin-left:-20px}.header-menu-wrapper .menu-cat-wrapper .menu-socials-responsive{display:flex;justify-content:space-around;width:175px;color:#fff;font-size:25px}.header-menu-wrapper .menu-cat-wrapper .menu-cat-item{padding:0;color:#fff;border:0}.header-menu-wrapper .menu-cat-wrapper .menu-cat-item.special .menu-cat-hover{left:0}.header-menu-wrapper .menu-cat-wrapper .menu-cat-item:hover .menu-cat-hover{width:100%}.header-menu-wrapper .menu-cat-wrapper .menu-cat-item .menu-cat-hover{height:3px;background:#fff}.header-menu-wrapper .menu-burger{display:block;position:absolute;right:30px}}.burger-btn{width:32px;height:32px;overflow:visible;cursor:pointer;fill:orangered}.burger-btn--1,.burger-btn--2,.burger-btn--3{transition:.2s;transform-origin:center center}.burger-btn.active .burger-btn--1{transform:translate(-7px, 7px) rotate(45deg)}.burger-btn.active .burger-btn--2{transform:translateX(20px);opacity:0}.burger-btn.active .burger-btn--3{transform:translate(-7px, -7px) rotate(-45deg)}

.header-wrapper{margin-bottom:50px;background-image:url(../../static/media/header-image.7a061f8d.jpg);min-height:100vh;width:100%;position:relative;transition:.3s all ease;background-repeat:no-repeat;background-position:center;background-size:100%}.header-wrapper:hover{background-size:105%;-webkit-filter:blur(0);filter:blur(0)}.header-wrapper:hover .header-filter{background-color:rgba(49,0,112,0.199)}.header-wrapper .header-filter{transition:.4s all ease;background-color:rgba(49,0,112,0.397);width:100%;height:100%;position:absolute}.header-wrapper .header-main{font-family:'Dancing Script', cursive;font-size:32px;color:#fff;position:absolute;z-index:1;bottom:25%;left:5%}.header-wrapper .header-main .header-subtitle{position:relative;left:80px;text-align:right}@media screen and (max-width: 768px){.header-wrapper{background-size:cover}.header-wrapper:hover{background-size:cover}.header-main h1,.header-main h2{font-size:40px}}@media screen and (max-width: 568px){.header-wrapper .header-main{text-align:center;position:relative;padding-top:60%;z-index:1;left:0;right:0;top:0;bottom:0}.header-wrapper .header-main .header-subtitle{text-align:center;position:static;padding-top:25px}}@media screen and (max-width: 450px){.header-wrapper .header-main{padding-top:90%}.header-wrapper .header-main h1,.header-wrapper .header-main h2{font-size:32px}}

